import { useState, useEffect } from 'react';
import axios from 'axios'
import { Link } from 'react-router-dom'

const MovieCredits = ( { match } ) => {

const [ movieCredits, setMovieCredits ] = useState({
  isFetched: false,
  data: {},
  error: null
})

  const [credits, setCredits] = useState({
    isFetched: false,
    data: {},
    error: null
  })

  const matchId = match.params.id;

useEffect(() => {
  axios.get(`https://api.themoviedb.org/3/person/${matchId}/movie_credits`, {
    params: {
      api_key: "a2a7da24dcc0311ab2440f05d2d45669"
    }
  })
    .then(function (response) {
      setMovieCredits({
        isFetched: true,
        data: response.data,
        error: false
      })
    })
    .catch(function (error) {
      setMovieCredits({
        isFetched: true,
        data: [],
        error: error
      })
    })
}, [])

  useEffect(() => {
    axios.get(`https://api.themoviedb.org/3/movie/${matchId}/credits`, {
      params: {
        api_key: "a2a7da24dcc0311ab2440f05d2d45669"
      }
    })
      .then(function (response) {
        setCredits({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setCredits({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, [])

  console.log(credits.data);

console.log(movieCredits.data);

  return(
    <>
      <div className="container">
        {
          movieCredits.isFetched ? (
            <div className="listOfMovies">
              {
                movieCredits.data.cast.map((movie, index) => (

                  <Link to={`/movie/${movie.id}`} className="card">
                    <span className="card-rating">{movie.vote_average}</span>
                    <div className="img-holder">
                      <img src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`} alt="" className="card-img" />
                    </div>
                    <h3 className="card-title">{movie.title}</h3>
                    <h5 className="card-date">{movie.release_date}</h5>
                  </Link>

                ))
              }
            </div>
          )
            : (
              <h1>Loading...</h1>
            )

        }
      </div>
    </>
  )
}

export default MovieCredits;