import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import Latest from './pages/latest';
import TopRated from './pages/topRated';
import UpComing from './pages/upcoming';
import PopularTv from './pages/popularTv';
import SingleMovie from './pages/SingleMovie';
import Home from './pages/Home';
import MovieCredits from './pages/MovieCredits';
import Recomendation from './pages/Recommendation';
import Movies from './pages/Movies/Movies';
import Search from './pages/Search/Search';

import Header from './containers/header'

function App() {
  return (
      <Router>
        <div className="app">

          <Header />

          <Switch>
            <Route exact path="/" component={ Home }/> 
            <Route exact path="/Movies" component={ Movies } />
            <Route exact path="/Latest" component={ Latest } />
            <Route exact path="/TopRated" component={ TopRated } />
            <Route exact path="/UpComing" component={ UpComing } />
            <Route exact path="/PopularTv" component={ PopularTv } />
            <Route exact path="/movie/:id" component={ SingleMovie } />
            <Route exact path="/person/:id/movie_credits" component={MovieCredits} />
            <Route exact path="/Recommendation_movie/:id" component={Recomendation} />
            <Route exact path="/search/:searchQuery" component={Search} />
          </Switch>

        </div>
      </Router>
  );
}

export default App;