import axios from 'axios';
import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

const Search = ( { match } ) => {

  const [movieList, setMovieList] = useState({
    isFetched: false,
    data: {},
    error: null
  })

  useEffect(() => {
    axios.get(`https://api.themoviedb.org/3/search/movie?query=${match.params.searchQuery}`, {
      params: {
        api_key: "a2a7da24dcc0311ab2440f05d2d45669"
      }
    })
      .then(function (response) {
        console.log('Result: ', response.data.results)
        setMovieList({
          isFetched: true,
          data: response.data,
          error: false
        })
      })
      .catch(function (error) {
        setMovieList({
          isFetched: true,
          data: [],
          error: error
        })
      })
  }, [])

  console.log(movieList);

  console.log(match.params.searchQuery);
  return (
    <>
      <div className="container">
        {
          movieList.isFetched ? (
            <div className="listOfMovies">
              {
                movieList.data.results.map((movie, index) => (

                  <Link to={`/movie/${movie.id}`} className="card">
                    <span className="card-rating">{movie.vote_average}</span>
                    <div className="img-holder">
                      <img src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`} alt="" className="card-img" />
                    </div>
                    <h3 className="card-title">{movie.title}</h3>
                    <h5 className="card-date">{movie.release_date}</h5>
                  </Link>

                ))
              }
            </div>
          ) : (
              <div>
                <h1>Bunaqa kino topilmadi :(</h1>
                <h1>Bunaqa kino topilmadi :(</h1>
                <h1>Bunaqa kino topilmadi :(</h1>
                <h1>Bunaqa kino topilmadi :(</h1>
                <h1>Bunaqa kino topilmadi :(</h1>
                <h1>Bunaqa kino topilmadi :(</h1>
              </div>
            )

        }
      </div>
    </>
  )
}

export default Search;